@import url(https://fonts.googleapis.com/css2?family=Merienda:wght@300;400;500;700&display=swap);
html, body {
  margin: 0;
  padding: 0;
  text-align: center;
}

h2 {
  font-style: italic;
}
*{
  font-family: 'Merienda', sans-serif;
}

.prime-title {
  font-size: 70px;
  color: rgba(151, 39, 5, 0.75);


}

.prime-button {
  width: 10em;
  color: rgb(199, 30, 11);
  text-transform: uppercase;
  font-weight: 600;
  font-size: 25px;
  border: 5px;
  background: linear-gradient(40deg, rgb(135, 231, 197), rgb(248, 244, 210));
  border-radius: 10px;
  padding: 10px 20px;
}
.prime-button:hover {
  color:beige;
  background: linear-gradient(250deg, rgb(247, 166, 100), rgb(79, 79, 80))
}

.secondary-button {
  color: rgb(201, 196, 196);
  background-color: rgb(94, 11, 2);
  font-weight: 500;
  border: none;
  margin: 5px;
  border-radius: 20px;
  padding: 10px 25px;
  text-transform: uppercase;
  font-size: 20px;
}

.secondary-button:hover {
  color: rgb(247, 237, 237);
  border: solid 5px rgb(20, 25, 20,);
}



/* Navbar */
nav {
  margin: 10px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

nav .nav-button {
  color :rgb(201, 241, 241);
  background-color: rgb(94, 11, 2);
  font-size: 30px;
  padding: 5px 5px;
  border-radius:25px;
  height: 100px;
  border: none;
  margin: 20px;
 
}
nav .logo-container{
  width: 150px;
  height: 150px;

}

nav .logo-container .logo {
  width: 100%;
}

nav .nav-button:disabled {
  background-color: rgb(201, 196, 196);
  color:black;
}

.logo-container img{
  width: 120px;
  height: 120px;
  border-radius: 40px;
}

/* Home page */

.overlay {
  width: 100vw;
  height: 100vh;
  position: fixed;
  background: linear-gradient( to bottom, rgba(47, 59, 2, 0.75) 0%,
  rgba(0, 0, 0, 0) 100%),
  url(/static/media/guy1.47633d0c.jpg);

}

.homepage {
  margin-top: 20vh;
}

/* AuthModal */

.auth-modal {
  position: absolute; 
  top: 50px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 40px;
  background-color: rgb(249, 218, 193);
  max-width: 360px;
  height: 600px;
  border-radius: 15px;

}

.auth-modal .close-icon {
  float: left;
}

.auth-modal form {
  display: flex;
  flex-direction: column;

}

.auth-modal input[type=password],
.auth-modal input[type=email] {
  padding: 7px;
  font-size: 15px;
  margin: 8px;
}




/* Signup */
.signup{
  background: linear-gradient( to bottom, rgba(47, 59, 2, 0.75) 0%,
  rgba(0, 0, 0, 0) 100%)

}
.signup form{
  justify-content: center;
  display: flex;


}

.signup form {
  border-top: solid 2px rgb(135, 231, 197);
}

.signup form section {
  display: flex;
  flex-direction: column;
  padding: 25px;
  /* text-align: start; */
  width: 40%;
}

.signup form input {
  margin: 8px 5px;
  font-size: 16px;
  padding: 10px 25px;
  border-radius: 15px;
}

.signup form .multiple-input-container {
  flex-direction: row;
  display: flex;
 
}

.signup form input::-webkit-outer-spin-button,
.signup form input::-webkit-inner-spin-button {
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}


.signup form .multiple-input-container label{
  padding: 12px;
  border: solid 2px rgb(249, 218, 193);
  border-radius: 8px;
  margin-right: 12px;
  transition: all 0.4s;
  
}

.signup form .multiple-input-container input[type="radio"] {
  display: none;
}

.signup form input[type="number"] {
  width: 25%;
}

.signup form .multiple-input-container input{
  margin-right: 15px;
}

.signup form input[type="radio"]:checked + label {
  border: solid 3px rgb(94, 11, 2);
}

.signup form input [type="submit"]:hover {
  background-color: rgb(5, 92, 62);
}
.signup form input [type="submit"]:active {
  background-color: rgb(179, 88, 78);
}

.signup form .photo-container {
  width: 100%;
}
